.card {
    position: relative;
    width: 163px;
    border-radius: 20px;
    img {
      width: 163px;
      height: 115px;
      border-radius: 6px;
    }
    .inner-text-container {
      position: absolute;
      bottom: 10px;
      margin-left: auto;
      margin-right: auto;
      left: 0;
      right: 0;
      color: #E5E5E5;
      font-size: 16px;
      p {
        margin-bottom: 0px !important;
        text-align: center;
        color: #F9F9F9;
      }
    }
  }