@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{height:11vh;display:flex;justify-content:left;align-items:center;padding-left:30px;padding-right:30px}.header .brand{font-size:40px;font-weight:700;line-height:50px;text-decoration:none;color:white}

.home .banner{height:90vh;background-image:url(/static/media/banner.61085ab9.png);background-repeat:no-repeat, repeat;background-color:#000000;background-position:center}.home .inner-container{height:79vh;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#f9f9f9}.home .inner-container h2{font-size:40px;line-height:48px;font-weight:normal !important;margin-top:0 !important;margin-bottom:0 !important}.home .inner-container .input-container{position:relative;display:flex;align-items:center;flex-direction:row;margin-top:20px}.home .inner-container .input-container>img{position:absolute;left:15px;cursor:pointer}.home .inner-container .form-control{display:block;width:500px;padding:0.375rem 0.75rem 0.375rem 2.75rem;font-size:1rem;font-weight:400;line-height:1.5;color:#212529;background-color:#fff;background-clip:padding-box;-webkit-appearance:none;-moz-appearance:none;appearance:none;border-radius:1.95rem}.home .inner-container .form-control:focus{outline:none}.home .main{background:#ffffff;padding:40px 77px}.home .main h4{font-weight:500;font-size:16px;line-height:19.36px;color:#000000;margin-bottom:20px !important}.home .main .vehicles{display:flex;flex-direction:row;justify-content:space-between}.home .main .vehicles .card{margin-right:8px;margin-bottom:10px}

.card{position:relative;width:163px;border-radius:20px}.card img{width:163px;height:115px;border-radius:6px}.card .inner-text-container{position:absolute;bottom:10px;margin-left:auto;margin-right:auto;left:0;right:0;color:#E5E5E5;font-size:16px}.card .inner-text-container p{margin-bottom:0px !important;text-align:center;color:#F9F9F9}

